import * as React from 'react';
import clsx from 'clsx';
import { withStyles, WithStyles } from '@material-ui/core';
import LazyLoad from 'react-lazyload';
import get from 'lodash/get';
import Style from './style';

interface Props extends WithStyles<typeof Style> {
	[name: string]: any;
	image: string;
	name: string;
	designation?: string;
	description?: string;
	small?: boolean;
	padding?: boolean;
}
/**
 * DesktopCard for founders section.
 * @param {any} props - Properties
 * @returns {React.Component} component.
 */
class DesktopCard extends React.PureComponent<Props> {
	componentDidMount() {}

	/**
	 * Render method
	 * @returns {JSX.Element} Jsx.
	 */
	public render() {
		const { classes, image, designation, description, padding, small = false } = this.props;
		const name = get(this.props, 'name', '');
		return (
			<div className={clsx({ [classes.root]: !small, [classes.small]: small })}>
				<LazyLoad height={282} offset={100}>
					<img
						src={image}
						alt="card"
						className={clsx(classes.card, {
							[classes.padding]: padding,
							[classes.heroImage]: !small,
							[classes.small]: small
						})}
					/>
				</LazyLoad>
				{!small && name !== '' && (
					<div className={classes.headline}>
						<span className={classes.lineHeight}>{name}</span>
					</div>
				)}
				{small && name !== '' && (
					<div className={classes.content}>
						<div className={classes.name}>{name}</div>
						<div className={classes.designation}>{designation}</div>
						<div className={classes.description}>{description}</div>
					</div>
				)}
			</div>
		);
	}
}

export default withStyles(Style, { name: 'DmartCorpFreshDesktopCard' })(DesktopCard);
